@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
/* https://nisnom.com/veb-razrabotki/css-button-hover-effects/ */

body {
    margin: 0;
    padding: 0;
    font-family: verdana;
  }
  .buttonLink {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 180px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 6px;
    border: 2px solid #1428e2;
    font-size: 24px;
    color: #1428e2;
    transition: 0.5s;
    overflow: hidden;
    background: transparent;
    font-family: 'Bangers', cursive;
  }
  .buttonLink:hover {
    color: rgb(236, 27, 27);
  }
  .buttonLink span {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(20, 40, 226, 0.5);
    transition: 0.5s;
    z-index: -1;
  }
  .buttonLink span:nth-child(1) {
    top: -100%;
    left: -100%;
  }
  .buttonLink:hover span:nth-child(1) {
    top: 0;
    left: 0;
  }
  .buttonLink span:nth-child(2) {
    bottom: -100%;
    left: -100%;
  }
  .buttonLink:hover span:nth-child(2) {
    bottom: 0;
    left: 0;
  }
  .buttonLink span:nth-child(3) {
    top: -100%;
    right: -100%;
  }
  .buttonLink:hover span:nth-child(3) {
    top: 0;
    right: 0;
  }
  .buttonLink span:nth-child(4) {
    bottom: -100%;
    right: -100%;
  }
  .buttonLink:hover span:nth-child(4) {
    bottom: 0;
    right: 0;
  }

  
