.flip-card {
  position: relative;
  width: 10%;
  min-width: 100px;
  height: 100px;
  perspective: 500px;
  display: block;
  margin: 10px;
  cursor: pointer;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.active .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  transition: all 0.5s ease-in;
  color: white;
  font-size: 10px;
  background-color: rgb(236, 227, 227);
  padding: 10px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  padding: 10px;
  border-radius: 5px;
}

.flip-card-front {
  background-color: #bbb;
  color: black;
}

.flip-card-back {
  background-color: dodgerblue;
  color: white;
  transform: rotateY(180deg);
}

